<template>
<div >
   <mu-appbar class="fixed" :title="videoObject.title">
     <mu-icon-button icon="arrow_back" slot="left" @click="goback()" />
   </mu-appbar>
<div style="padding-top:66px;">
 <mu-card  >
   <mu-card-media >
    <div id="player" class="player">
          <video-player  class="vjs-custom-skin"
                         ref="videoPlayer"
                         :options="playerOptions"
                         :playsinline="true">
                       
          </video-player>
        </div>
   
  
  </mu-card-media>
 </mu-card>
  <div id="chatview" style="position:absolute;top:0;bottom:0;left:0;right:0;">
      <iframe :src="chaturl" width="100%" height="100%"></iframe>
  </div>


</div>
<mu-dialog :open="dialog" title="广告">
    <img :src="videoObject.src" width="300px" height="auto">
    <mu-flat-button  label="关闭" slot="actions" primary @click="()=>{dialog=false}"/>
  </mu-dialog>
</div>
</template>
<script >
  export default {
    name:'livevideo',

    mounted(){
      
      document.getElementById("chatview").style.top=66+document.getElementById("player").offsetWidth*9/16+"px"
      
      //this.chatview.style.top=this.media.height+"px";
      
    },
    
    data(){
      return {
        currentTab:'tab1',
        dialog:false,
        videoplayerurl:'',

      }
    },
   
    beforeRouteEnter(to,from,next)
    {
      next(true)
    },

    beforeRouteLeave(to,from,next)
    {
       
       next(true)
     

    },
    computed:{
       videoObject() {
        return this.$store.state.cvo
       },
       playerOptions() {

        return {
          aspectRatio:'16:9',
          autoplay:false,
          sources: [{
            type: "video/mp4",
            src: this.$store.state.cvo.mp4
          }],
          poster: this.$store.state.cvo.poster,
        }
      },
      chaturl(){
        if(this.$store.state.loginedUser){
          return "http://104.201.54.66:8000?role=user&username="+this.$store.state.loginedUser.username
        }
        
        return "http://104.201.54.66:8000?role=guest&username=游客"+Math.floor(Math.random()*100000)
      }
    },
    methods: {

      fetchData () {
        alert('hello')
      },
      handleTabChange (val) {
        this.currentTab = val
      },
      goback()
      {
        this.$router.go(-1)
      }
     

    } 
  }
</script>
<style scoped>
  
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
   }
   
   .desc-container{
    height:100%;
    overflow-y: scroll;
   }
   img {
    width: 100%;
   }
   .fixed {
    position:fixed;
    top: 0;
   }
 
</style>


